export function loadObjectFromSessionStorage(valueName: string) {
    try {
        const serializedValue = sessionStorage.getItem(`${valueName}`);
        if (serializedValue === null || serializedValue === undefined || serializedValue === 'undefined') {
            return undefined;
        }

        return JSON.parse(serializedValue);
    } catch (err) {
        console.error(err);
        return undefined;
    }
}

export function loadValueFromSessionStorage(valueName: string) {
    try {
        const value = sessionStorage.getItem(`${valueName}`);
        if (value === null || value === undefined || value === 'undefined') {
            return undefined;
        }

        return value;
    } catch (err) {
        console.error(err);
        return undefined;
    }
}

export function loadObjectFromLocalStorage(valueName: string) {
    try {
        const serializedValue = localStorage.getItem(`${valueName}`);
        if (serializedValue === null || serializedValue === undefined || serializedValue === 'undefined') {
            return undefined;
        }

        return JSON.parse(serializedValue);
    } catch (err) {
        console.error(err);
        return undefined;
    }
}

export function loadValueFromLocalStorage(valueName: string) {
    try {
        const value = sessionStorage.getItem(`${valueName}`);
        if (value === null || value === undefined || value === 'undefined') {
            return undefined;
        }

        return value;
    } catch (err) {
        console.error(err);
        return undefined;
    }
}

export function saveValueToLocalStorage(valueName: string, value: any) {
    try {
        const serializedState = JSON.stringify(value);
        localStorage.setItem(`${valueName}`, serializedState);
    } catch (err) {
        console.error(err);
    }
}

export function saveValueToSessionStorage(valueName: string, value: any) {
    try {
        sessionStorage.setItem(`${valueName}`, value);
    } catch (err) {
        console.error(err);
    }
}